  margin-top: 1rem;
}
.modern-app-store-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  background: white;
  border: 2px solid #e5e7eb;
  border-radius: 0.75rem;
  text-decoration: none;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
}
.modern-app-store-btn::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: var(--store-color);
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 0.5rem;
}
.modern-app-store-btn:hover {
  transform: translateY(-2px);
  border-color: var(--store-color);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.15);
}
.modern-app-store-btn:hover::before {
  opacity: 0.05;
}
.app-store-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
  background: var(--store-color);
  border-radius: 0.5rem;
  color: white;
  transition: all 0.3s ease;
  z-index: 1;
  position: relative;
}
.modern-app-store-btn:hover .app-store-icon {
  background: var(--store-hover-color);
  transform: scale(1.05);
}
.app-store-text {
  display: flex;
  flex-direction: column;
  gap: 0.125rem;
  z-index: 1;
  position: relative;
}
.app-store-name {
  font-size: 0.875rem;
  font-weight: 600;
  color: #111827;
  transition: color 0.3s ease;
}
.app-store-desc {
  font-size: 0.75rem;
  color: #6b7280;
  transition: color 0.3s ease;
}
.modern-app-store-btn:hover .app-store-name {
  color: var(--store-color);
}
.modern-app-store-btn:hover .app-store-desc {
  color: #374151;
}
.f-brand-i-social-icon {
  width: 1.625rem;
  height: 1.625rem;
}
.f-brand-i-social-i-box svg {
  width: 100%;
  height: 100%;
}
.f-brand-i-social-i-box svg rect {
  fill: #000 !important;
}
.f-brand-i-social-i-box svg text {
  fill: #fff !important;
}
.f-brand-i-social-i-box svg path {
  fill: #fff !important;
  stroke: #fff !important;
}
.f-brand-i-social-i-box svg circle {
  fill: #fff !important;
}
.f-brand-i-social-i-box svg * {
  fill: #fff !important;
  stroke: #fff !important;
}
.f-links {
  display: flex;
  flex-direction: column;
}
.f-links-ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}
.f-link-li {
  list-style: none;
}
.f-link-h {
  font-size: 1rem;
  font-weight: 600;
  color: #111827;
  margin-bottom: 1rem;
}
.f-link-a {
  color: #6b7280;
  text-decoration: none;
  font-size: 0.875rem;
  transition: color 0.2s;
}
.f-link-a:hover {
  color: #111827;
}
.f-link-p {
  color: #6b7280;
  font-size: 0.875rem;
  margin-bottom: 1rem;
  line-height: 1.6;
}
.f-link-p-black {
  color: #111827;
  font-weight: 500;
  font-size: 0.875rem;
}
.blue {
  color: var(--green-dark);
  cursor: pointer;
}
.f-contact-app-box {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.f-mail-section {
  margin-bottom: 1rem;
}
.f-contact-info {
  margin-bottom: 1.5rem;
}
.f-app-section {
  margin-top: 1rem;
}
.f-mobile-app-icons {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.f-mobile-app-i-box {
  display: inline-block;
}
.f-mobile-app-icon {
  height: auto;
  width: 129px;
  transition: transform 0.2s;
}
.f-mobile-app-i-box:hover .f-mobile-app-icon {
  transform: translateY(-2px);
}
.f-bottom-box {
  padding: 1.5rem 0;
  border-top: 1px solid #e5e7eb;
}
.f-copyright-text {
  text-align: center;
  color: #6b7280;
  font-size: 0.875rem;
}
.bottom-fixed-menu {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: white;
  border-top: 1px solid #e5e7eb;
  z-index: 50;
  padding: 0.75rem 0;
}
.btm-fxd-link {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-decoration: none;
  color: #6b7280;
  transition: color 0.2s;
}
.btm-fxd-link:hover {
  color: var(--green-dark);
}
.btm-fxd-img {
  width: 1.5rem;
  height: 1.5rem;
  margin-bottom: 0.25rem;
}
.swiper {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 768px) {
  .h-input {
    width: 100%;
    max-width: 20rem;
  }
  .cty-products {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 1rem;
  }
  .h-main-content {
    flex-direction: column;
    gap: 1rem;
  }
  .h-user-section {
    flex-wrap: wrap;
    justify-content: center;
  }
  .footer-grid {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  .f-brand-info-box {
    max-width: none;
    text-align: center;
  }
  .f-brand-i-social-i-boxes {
    justify-content: center;
  }
  .f-top-content {
    flex-direction: column;
    gap: 1rem;
    align-items: center;
  }
  .f-payment-methods {
    flex-wrap: wrap;
    justify-content: center;
  }
  .f-contact-app-box {
    text-align: center;
  }
  .f-mobile-app-icons {
    align-items: center;
  }
}
@media (max-width: 1024px) and (min-width: 769px) {
  .footer-grid {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  .f-brand-info-box {
    grid-column: 1 / -1;
    max-width: none;
    text-align: center;
  }
  .f-contact-app-box {
    grid-column: 1 / -1;
    flex-direction: row;
    justify-content: space-around;
    align-items: flex-start;
  }
}
@media (max-width: 640px) {
  .container-md {
    padding: 0 0.5rem;
  }
  .h-top-bar-content {
    flex-direction: column;
    gap: 0.5rem;
  }
  .h-top-links {
    flex-wrap: wrap;
    justify-content: center;
  }
  .h-top-right {
    flex-wrap: wrap;
    justify-content: center;
  }
  .h-nav-content {
    flex-direction: column;
    gap: 1rem;
  }
  .h-nav-links {
    flex-wrap: wrap;
    justify-content: center;
  }
}
